<template>
    <div>
        <Head bol='重置密码' headback='false' headto='false'></Head>
        <div class="main">
            <ul class="login">
                <li><input type="text" v-model="username" placeholder="账号"></li>
                <li>
                    <input type="text" v-model="oldpassword" placeholder="旧密码">
                </li>
                <li>
                    <input type="text" v-model="newpassword" placeholder="请输入新密码">
                </li>
                <li>
                    <input type="text" v-model="newpassword1" placeholder="请确认密码">
                </li>
                <li>
                    <input type="text" v-model='proving' placeholder="验证码">
                    <div class="provings">
                        <img :src="provingimg" alt="">
                        <div>
                            <p>看不清</p>
                            <p @click="getpro()">换一张</p>
                        </div>
                    </div>
                </li>
            </ul>
            <button @click="register()">确认修改</button>
            <!-- 弹出框 -->
            <div class="modals" v-if="!warnbol">
                <div class="warn">
                    <span></span>
                    <span></span>
                </div>
                <div class="warn-con">{{warn}}</div>
                <div class="btn" @click="getbtn()">确定</div>
            </div>
        </div>
    </div>
</template>

<script>
import Head from './../header/Head'
import axios from "axios"
axios.defaults.withCredentials=true
export default {
    components: {
        Head
    },
    data () {
        return {
            warnbol:true,
            provingimg:'',
            warn:'',
            username:'',
            oldpassword:'',
            newpassword:'',
            newpassword1:'',
            proving:''
        }
    },
    methods: {
         //点击换一张切换验证码，通过接口获取验证码
      getpro(){
          var that = this
          this.http.post('http://elm.cangdu.org/v1/captchas',{}).then(res => {
              console.log(res.code)
              if(res.code!=''){
                  this.provingimg=res.code
              }
          })
      },
      //取消弹出框
      getbtn(){
          this.warnbol=true
          if(this.warn=='密码修改成功'){
              this.$router.push({path:'/User'})
          }
      },
      //点击确认修改按钮，修改密码
      register(){
          if(this.username!=""&&this.oldpassword!=""&&this.newpassword!=''&&this.newpassword1!=''&&this.newpassword==this.newpassword1&&this.proving!=''){
              axios.post('http://elm.cangdu.org/v2/changepassword',{captcha_code:this.proving,confirmpassword:this.newpassword1,newpassword:this.newpassword,oldpassWord:this.oldpassword,username:this.username}).then(res=>{
                  if(res.status==0){
                      if(res.message=='密码不正确'){
                          this.warnbol=false
                          this.warn='密码不正确'
                          this.getpro()
                      }
                      if(res.message=='验证码不正确'){
                          this.warnbol=false
                          this.warn='验证码不正确'
                          this.getpro()
                      }
                      if(res.message=='未找到当前用户'){
                          this.warnbol=false
                          this.warn='未找到当前用户'
                          this.getpro()
                      }
                  }
                   if(res.status==1){
                      if(res.success=='密码修改成功'){
                          this.warnbol=false
                          this.warn='密码修改成功'
                          this.getpro()
                          if (localStorage.getItem("vuex")) {
                             this.$store.state.users = JSON.parse(localStorage.getItem("vuex"));
                             for(let i in this.$store.state.users.users){
                                 this.$store.state.users.users[i].pass=this.newpassword
                                 localStorage.setItem('vuex',JSON.stringify(this.$store.state.users))
                             }
                           }
                      }
                  }
              })
          }
          if(this.username==''){
              this.warnbol=false
              this.warn='请输入正确的账号'
              this.getpro()
          }
          if(this.username!=''&&this.oldpassword==""){
              this.warnbol=false
              this.warn='请输入旧密码'
              this.getpro()
          }
          if(this.username!=''&&this.oldpassword!=""&&this.newpassword==''){
              this.warnbol=false
              this.warn='请输入新密码'
              this.getpro()
          }
          if(this.username!=''&&this.oldpassword!=""&&this.newpassword!=''&&this.newpassword1==''){
              this.warnbol=false
              this.warn='请确认新密码'
              this.getpro()
          }
          if(this.username!=''&&this.oldpassword!=""&&this.newpassword!=this.newpassword1){
              this.warnbol=false
              this.warn='两次输入的密码不一致'
              this.getpro()
          }
          if(this.username!=""&&this.oldpassword!=""&&this.newpassword!=''&&this.newpassword1!=''&&this.proving==''){
              this.warnbol=false
              this.warn='请输入验证码'
              this.getpro()
          }
          
      }
    },
    mounted () {
        this.getpro()
    }
}
</script>

<style lang="scss" scoped>
.main {
  width: 100%;
  height: 100%;
  padding-top: 0.88rem;
  .login {
    width: 100%;
    height: auto;
    margin-top: 0.28rem;
    background: white;
    li {
      width: 6.94rem;
      height: 0.4rem;
      padding: 0.37rem 0.28rem;
      border-bottom: 0.01rem solid #ccc;
      display: flex;
      input {
        width: 60%;
        height: 0.4rem;
        border: none;
        outline: none;
        display: block;
        font-size: 0.38rem;
      }
      .switch1 {
        margin-left: 2rem;
      }
      .provings{
          width: 40%;
          height: 0.7rem;
          display: flex;
          margin-top: -0.15rem;
          img {
              width: 1.64rem;
              height: 0.7rem;
              margin-right: 0.08rem;
          }
          div {
              width: 0.92rem;
              p {
                  width: 100%;
                  font-size: 0.26rem;
                  margin-bottom: 0.06rem;
              }
              p:nth-child(2){
                  color: #3b95e9;
              }
          }
      }
    }
  }
  button {
      width: 6.94rem;
      height: 0.9rem;
      margin: 0 auto;
      border: none;
      border-radius: 0.06rem;
      background: #4cd964;
      color: white;
      font-size: 0.4rem;
  }
  .modals{
      width: 5.62rem;
      height: 4rem;
      position: fixed;
      left: 0.94rem;top: 0.4rem;
      background: white;
      z-index: 5;
      border-radius: 0.1rem;
      .warn {
          width: 1.4rem;
          height: 1.4rem;
          border-radius: 50%;
          border: 0.07rem solid #f8cb86;
          margin: 0 auto;
          margin-top: 0.28rem;
          span:nth-child(1){
              width: 0.06rem;
              height: 0.7rem;
              background: #f8cb86;
              display: block;
              margin: 0 auto;
              margin-top: 0.2rem;
          }
          span:nth-child(2){
              width: 0.1rem;
              height: 0.1rem;
              border-radius: 50%;
              background: #f8cb86;
              display: block;
              margin: 0 auto;
              margin-top: 0.1rem;
          }
      }
      .warn-con{
          width: 4.26rem;
          height: 0.46rem;
          margin: 0 auto;
          margin-top: 0.38rem;
          font-size: 0.36rem;
      }
      .btn {
          width: 100%;
          height: 0.86rem;
          background: #4cd964;
          margin-top: 0.45rem;
          color: white;
          font-size: 0.46rem;
          line-height: 0.86rem;
          border-bottom-left-radius: 0.1rem;
          border-bottom-right-radius: 0.1rem;
      }
  }
}
</style>



